.sidebar {
  background-color: #1e293b; /* Darker shade for better contrast */
  color: white;
  height: 100vh;
  padding: 10px 0; /* Reduce padding around the sidebar */
  width: 250px; /* Expanded width */
  transition: width 0.3s ease; /* Smooth transition for width */
  overflow: hidden; /* Hide scrollbars */
  display: flex;
  flex-direction: column; /* Align items vertically */
  justify-content: flex-start; /* Align items at the start */
}

.sidebar.collapsed {
  width: 80px; /* Collapsed width */
}

.sidebar .title {
  font-size: 1.25rem; /* Adjust font size for the title */
  font-weight: bold;
  text-align: center;
  margin-bottom: 15px; /* Reduce space below the title */
}

.sidebar ul {
  list-style: none;
  padding: 0;
  margin: 0; /* Remove default margin */
  flex: 1; /* Allow the ul to grow and fill the available space */
  display: flex;
  flex-direction: column;
  align-items: flex-start; /* Align items to the start */
  justify-content: flex-start; /* Align items vertically to the top */
}

.sidebar ul li {
  padding: 8px 15px; /* Adjust padding for better alignment */
  display: flex;
  align-items: center;
  transition: opacity 0.3s ease; /* Smooth transition for opacity */
  width: 100%;
}

.sidebar.collapsed ul li {
  justify-content: center; /* Center content horizontally in collapsed state */
}

.sidebar ul li a {
  color: #cfd8dc;
  text-decoration: none;
  display: flex;
  align-items: center;
  width: 100%; /* Ensure link takes up full width */
  overflow: hidden; /* Hide overflowing text */
  white-space: nowrap; /* Prevent text from wrapping */
  transition: opacity 0.3s ease; /* Smooth transition for opacity */
}

.sidebar.collapsed ul li a {
  padding: 8px 0; /* Adjust padding when collapsed */
}

.sidebar.collapsed ul li a span {
  display: none; /* Hide text in collapsed state */
}

.sidebar ul li a:hover {
  color: white;
}

/* Icon styling */
.sidebar ul li svg {
  margin-right: 10px; /* Reduce space between icon and text */
  font-size: 1.25rem; /* Adjust icon size if needed */
  transition: margin-right 0.3s ease; /* Smooth transition for icon spacing */
}

.sidebar.collapsed ul li svg {
  margin-right: 0; /* Remove margin when collapsed */
}

/* Additional styles to ensure proper alignment */
.sidebar ul li {
  justify-content: flex-start;
  align-items: center;
}
