/* products.css */

.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 16px;
    background-color: #1f2937; /* Dark background */
    color: #ffffff;
    border-radius: 8px;
  }
  
  .stats {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 16px;
    margin-bottom: 24px;
  }
  
  .stat-item {
    padding: 16px;
    background-color: #374151; /* Darker gray */
    text-align: center;
    border-radius: 8px;
  }
  
  .stat-item p {
    margin-bottom: 8px;
  }
  
  .stat-item h3 {
    font-size: 20px;
    font-weight: bold;
  }
  
  .search-bar {
    display: flex;
    align-items: center;
    margin-bottom: 16px;
  }
  
  .search-bar input {
    width: 100%;
    padding: 8px;
    background-color: #374151;
    color: #ffffff;
    border: none;
    border-radius: 8px;
    outline: none;
  }
  
  .search-bar input:focus {
    border: 2px solid #3b82f6; /* Blue border on focus */
  }
  
  .search-bar svg {
    width: 24px;
    height: 24px;
    margin-left: 8px;
    color: #ffffff;
  }
  
  .table-container {
    overflow-x: auto;
  }
  
  table {
    width: 100%;
    border-collapse: collapse;
    background-color: #374151;
    border-radius: 8px;
  }
  
  thead {
    background-color: #4b5563;
  }
  
  th, td {
    padding: 12px;
    text-align: left;
  }
  
  th {
    font-weight: bold;
  }
  
  tbody tr {
    border-bottom: 1px solid #4b5563;
  }
  
  tbody tr:last-child {
    border-bottom: none;
  }
  
  tbody td {
    display: flex;
    align-items: center;
  }
  
  tbody td svg {
    width: 20px;
    height: 20px;
    cursor: pointer;
  }
  
  tbody td .edit-icon {
    color: #3b82f6; /* Blue */
  }
  
  tbody td .delete-icon {
    color: #ef4444; /* Red */
    margin-left: 8px;
  }
  
  tbody td .edit-icon:hover {
    color: #2563eb; /* Darker blue on hover */
  }
  
  tbody td .delete-icon:hover {
    color: #dc2626; /* Darker red on hover */
  }
  